import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ProcessHttpmsgProvider } from '../process-httpmsg/process-httpmsg';
import { nearestStation } from '../../shared/nearestStation';
import { Observable } from 'rxjs/Observable';
import { baseURL } from '../../shared/baseurl';
/*
  Generated class for the NearestStationProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class NearestStationProvider {

  constructor(public http: HttpClient, private processHttpMsgService: ProcessHttpmsgProvider) {
    console.log('Hello NearestStationProvider Provider');
  }

  getNearestStationCorrds(body): Observable<nearestStation> {
    return this.http.post( baseURL + 'nearest_station', body)
      .map(res => { return this.processHttpMsgService.extractData(res); })
      .catch((err) => { return this.processHttpMsgService.handleError(err); });

  }

}



